$header-color: rgb(252, 212, 232);

body {
    margin: 0;
    font-family: Arial, sans-serif;
    background-color: black;
    box-sizing: border-box;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.625rem 3.125rem; /* Replaced px-to-rem(10px) px-to-rem(50px) */
    background-color: #fff;
    height: 4rem; /* Replaced px-to-rem(64px) */
    width: 100%;
    box-sizing: border-box;

    a {
        text-decoration: none;

        .logo {
            display: flex;
            flex-direction: column;
            align-items: center;
            font-weight: bold;
            
            .logo-winner {
                font-size: 2.25rem; /* Replaced px-to-rem(36px) */
                color: darken($header-color, 15%);
            }
            
            .logo-lash-studios {
                font-size: 1.25rem; /* Replaced px-to-rem(20px) */
                color: darken($header-color, 10%);
            }
        }
    }

    .search-container {
        display: flex;
      
        input, button {
            border: none;
            padding: 0.625rem; /* Replaced px-to-rem(10px) */
            border-radius: 1.25rem; /* Replaced px-to-rem(20px) */
            margin-right: 0.3125rem; /* Replaced px-to-rem(5px) */
        }
        
        input {
            flex-grow: 2;
            width: 25rem; /* Replaced px-to-rem(400px) */
            background-color: rgba(128, 128, 128, 0.128);
        }
      
        button {
            background-color: $header-color;
            color: #fff;
            cursor: pointer;
            box-shadow: 0 0.25rem 0.5rem rgba(0, 0, 0, 0.1); /* Replaced px-to-rem(4px) px-to-rem(8px) */
        }
    }

    .nav-links {
        display: flex;
        gap: 2rem; /* Replaced px-to-rem(32px) */

        button {
            background-color: transparent;
            border: none;
            padding: 0;
            margin: 0;
            cursor: pointer;
            color: black;
            text-decoration: none;
            font-family: inherit;
            font-size: inherit;
            line-height: inherit;
    
            &:hover, &:focus {
                text-decoration: underline;
            }
    
            &:disabled {
                color: grey;
                cursor: not-allowed;
            }
        }
    }

    .icons {
        display: flex;
        gap: 1.25rem; /* Replaced px-to-rem(20px) */
      
        .cart-icon, .profile-icon {
            cursor: pointer;
        }

        select {
            border: none;
            cursor: pointer;
            background-color: transparent;
        }
    }

    @media (max-width: 576px) {
        flex-direction: column;
        padding: 0.625rem; /* Replaced px-to-rem(10px) */
        height: auto;
        width: 100%;
    
        .nav-links, .search-container {
            flex-direction: column;
            gap: 0.625rem; /* Replaced px-to-rem(10px) */
        }
    
        .logo-winner {
            font-size: 1.5rem; /* Replaced px-to-rem(24px) */
        }
    
        .logo-lash-studios {
            font-size: 1rem; /* Replaced px-to-rem(16px) */
        }
    
        .icons {
            gap: 0.625rem; /* Replaced px-to-rem(10px) */
        }
    }
    

    //gotta focus on this stuff fam before u send the code to the dude
    @media (min-width: 577px) and (max-width: 769px) {
        padding: px-to-rem(10px) px-to-rem(350px);

        .nav-links {
            gap: px-to-rem(20px);
        }

        .logo-winner {
            font-size: px-to-rem(30px);
        }

        .logo-lash-studios {
            font-size: px-to-rem(18px);
        }

        .icons {
            gap: px-to-rem(15px);
        }
    }

    @media (min-width: 770px) and (max-width: 834px) {
        padding: px-to-rem(10px) px-to-rem(40px); // Slightly more padding than smaller screens

        .nav-links {
            gap: px-to-rem(25px); // Adjust gap
        }

        .logo-winner {
            font-size: px-to-rem(33px); // Adjust font size
        }

        .logo-lash-studios {
            font-size: px-to-rem(19px); // Adjust font size
        }

        .icons {
            gap: px-to-rem(18px); // Adjust gap
        }
    }

    @media (min-width: 835px) and (max-width: 1024px) {
        padding: px-to-rem(10px) px-to-rem(45px); // Increase padding for larger screens

        .nav-links {
            gap: px-to-rem(28px); // Increase gap
        }

        .logo-winner {
            font-size: px-to-rem(35px); // Slightly larger font size
        }

        .logo-lash-studios {
            font-size: px-to-rem(21px); // Slightly larger font size
        }

        .icons {
            gap: px-to-rem(20px); // Adjust gap
        }
    }

    @media (min-width: 1025px) {
        // Adjustments for large screens, might retain original styles or make minimal changes
    }
  }